/*
 * media queries
 * small:         30em
 * medium:        48.75em
 * medium-large:  61.25em
 * large:         75em
 * x-large:       87.5em
 */
.icon-heading-list {
  list-style: none;
  padding: 0;
  margin-left: auto;
  margin-right: auto;
  text-align: left; }
  [class*='u-grid--'] > .icon-heading-list {
    padding-top: 1rem; }
  @media (min-width: 30em) {
    .icon-heading-list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between; } }

.icon-heading-list__item {
  font-size: 1.4rem;
  line-height: 1.7;
  margin: 0; }
  .icon-heading-list__item > :first-child {
    margin-top: 0; }
  .icon-heading-list__item > :last-child {
    margin-bottom: 0; }
  .icon-heading-list__item:nth-child(n + 2) {
    margin-top: 2rem; }
  @media (min-width: 30em) {
    .icon-heading-list__item {
      width: calc(50% - 2rem); }
      .icon-heading-list__item:nth-child(2) {
        margin-top: 0; }
      .icon-heading-list__item:nth-child(n + 3) {
        margin-top: 2rem; } }
  @media (min-width: 30em) {
    .icon-heading-list__item {
      width: calc(50% - 4rem); } }
  @media (min-width: 48.75em) {
    .icon-heading-list__item {
      width: calc(33.3334% - 4rem); }
      .icon-heading-list__item:nth-child(3) {
        margin-top: 0; } }
  @media (min-width: 87.5em) {
    .icon-heading-list__item {
      width: calc(33.3334% - 6rem); } }

.icon-heading-list__heading {
  align-items: center;
  color: #237c9a;
  display: flex;
  font: 1.4rem/1.3 "Kapra Neue Expanded";
  margin-bottom: -0.8rem;
  text-transform: uppercase; }
  @media (min-width: 48.75em) {
    .icon-heading-list__heading {
      font-size: 1.6rem; } }
  @media (min-width: 75em) {
    .icon-heading-list__heading {
      font-size: 1.8rem; } }
  .icon-heading-list__heading img {
    height: 4.5rem;
    margin-right: 1.6rem;
    width: auto; }

.icon-heading-list__link {
  display: block; }
  .icon-heading-list__link.-heading {
    text-decoration: none; }
    .icon-heading-list__link.-heading:focus .icon-heading-list__heading, .icon-heading-list__link.-heading:hover .icon-heading-list__heading {
      text-decoration: underline; }
    .icon-heading-list__link.-heading .icon-heading-list__heading {
      margin: 0; }
  .icon-heading-list__link.-more {
    padding-right: 18px;
    position: relative;
    text-decoration: none;
    display: inline-block;
    font-size: 1.4rem;
    display: inline-block; }
    .icon-heading-list__link.-more::after {
      content: "";
      display: block;
      position: absolute;
      background: url(..//images/arrow-link--orange.svg) no-repeat;
      height: 12px;
      margin-top: -6px;
      right: 4px;
      top: 50%;
      transition: all 0.2s cubic-bezier(0.75, 0, 0.125, 1);
      width: 7px; }
    .icon-heading-list__link.-more:focus, .icon-heading-list__link.-more:hover {
      text-decoration: underline; }
    .icon-heading-list__link.-more:focus::after, .icon-heading-list__link.-more:hover::after {
      right: 0; }
