.icon-heading-list {
  @include reset-list;
  margin-left: auto;
  margin-right: auto;
  text-align: left;

  [class*='u-grid--'] > & {
    padding-top: unit(0.5);
  }

  @include respond-to('small') {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.icon-heading-list__item {
  @include null-margins;
  font-size: 1.4rem;
  line-height: 1.7;
  margin: 0;

  &:nth-child(n + 2) {
    margin-top: unit(1);
  }

  @include respond-to('small') {
    width: calc(50% - #{unit(1)});

    &:nth-child(2) {
      margin-top: 0;
    }

    &:nth-child(n + 3) {
      margin-top: unit(1);
    }
  }

  @include respond-to('small') {
    width: calc(50% - #{unit(2)});
  }

  @include respond-to('medium') {
    width: calc(33.3334% - #{unit(2)});

    &:nth-child(3) {
      margin-top: 0;
    }
  }

  @include respond-to('x-large') {
    width: calc(33.3334% - #{unit(3)});
  }
}

.icon-heading-list__heading {
  align-items: center;
  color: $color--jelly-bean;
  display: flex;
  font: 1.4rem/1.3 $font-family--kapra-expanded;
  margin-bottom: unit(-0.4);
  text-transform: uppercase;

  @include respond-to('medium') {
    font-size: 1.6rem;
  }

  @include respond-to('large') {
    font-size: 1.8rem;
  }

  img {
    height: unit(2.25);
    margin-right: unit(0.8);
    width: auto;
  }
}

.icon-heading-list__link {
  display: block;

  &.-heading {
    text-decoration: none;

    @include hover {
      .icon-heading-list__heading {
        text-decoration: underline;
      }
    }

    .icon-heading-list__heading {
      margin: 0;
    }
  }

  &.-more {
    @include arrow-link('orange');
    display: inline-block;
  }
}
